var Job_index;
var Job_Title=[];//标题数组
var Job_Content=[];//正文数组

export default {
	data() {
		return {
			
		}
	},
	methods: {
		det(){
			uni.navigateTo({
				url:'../det/det'
			})
		},
		Publish(){
			uni.navigateTo({
				url:"../Publish_Editor/Publish_Editor"
			})
		}
	},
	onLoad() {
		Job_Title=[];//标题数组
		Job_Content=[];//正文数组
		uni.getStorage({
			key:"PJ_ID",
			success: (res) => {
				Job_index = res.data;
				console.log(Job_index);
				for (var i = 1;i <= Job_index ;i++) {
					uni.getStorage({
						key:i + "PJ_Title",
						success: (res) => {
							Job_Title.push(res.data);
							console.log(Job_Title);
							}
						})
					uni.getStorage({
						key:i + "PJ_Content",
						success: (res1) => {
							Job_Content.push(res1.data);
							console.log(Job_Content);
						}
					})
				}
			}
		})
	}
}
